.heading {
  font-size: 25px;
  color: #373537;
  font-weight: 600;
  margin-bottom: 20px;
}
.links {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.description {
  font-size: 16px;
  line-height: 1.5;
  padding-bottom: 15px;
}
.presentation {
  margin-top: 35px;
  padding: 10px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  background-color: rgb(79 90 93 / 18%);
}

.photo {
  width: 50px;
  border-radius: 50%;
}
.linkedin,
.github {
  position: relative;
  text-decoration: none;
  color: #373537;
  cursor: pointer;
}
.linkedin {
  display: flex;
}
.info {
  width: 100%;
  margin-left: 10px;
}
.github {
  margin-left: 10px;
  transition: color 0.25s;
}
.github::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.8px;
  background-color: #373537;
  transition: background-color 0.25s;
}
.github:hover {
  position: sticky;
  color: red;
  font-size: 18px;
  font-weight: 700;
}
.github:hover.github::after {
  background-color: red;
}

.demo {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
  margin-left: 190px;
  color:black;
  background-color: rgb(79 90 93 / 18%);
  border-radius: 7px;
}
.demo:hover  {
background-color: rgba(9, 239, 255);
color: red;
font-weight: bold;
}
